import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, NavParams, ViewController } from 'ionic-angular';
import { SpecializationsProvider } from '../../../providers/specializations/specializations';
import { SpecializationMapper } from '../../../models/specialization.model';

/**
 * Generated class for the SpecializationSearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name: 'SpecializationSearchPage'
})
@Component({
  selector: 'page-specialization-search',
  templateUrl: 'specialization-search.html',
})
export class SpecializationSearchPage {
  searching: boolean = false;

  specializationName: string;

  currentItems: SpecializationMapper[];

  originalItems: SpecializationMapper[];

  specializationSelected: SpecializationMapper[] = [];

  constructor(public navCtrl: NavController, private viewCtrl: ViewController,  public navParams: NavParams,
              public specializationTypesPrv: SpecializationsProvider,
              public modalCtrl: ModalController) {
    this.specializationSelected = this.navParams.get('specializationSelected') ?
      this.navParams.get('specializationSelected') :
      this.specializationSelected;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SpecializationSearchPage');
    this.initializeItems();
  }

  initializeItems() {
    this.searching = true;
    this.specializationTypesPrv.query().subscribe((data: SpecializationMapper[]) => {
      this.searching = false;
      this.originalItems = data;
      this.currentItems = this.originalItems;
    });
  }

  onCancel() {
    this.currentItems = this.originalItems;
  }

  getSpecializations(ev: any) {
    const val = ev.target.value;
    // Reset items back to all of the items
    if (!this.originalItems || !this.originalItems.length) {
      return;
    }

    if (val === '' ) {
      this.currentItems = this.originalItems;
    }

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.currentItems = this.originalItems.filter((item: any) => {
        return item.name.toLowerCase().indexOf(val.toLowerCase()) >= 0;
      })
    }
  }

  dismiss() {
    console.log('this.specializationSelected', this.specializationSelected);
    this.viewCtrl.dismiss({ specializationSelected: this.specializationSelected });
  }

  addSpecialization(specialization: SpecializationMapper) {
    if (specialization.clinics) {
      delete specialization.clinics;
    }

    console.log('specialization', specialization);

    if (specialization.checked) {
      this.specializationSelected.push(specialization);
      return;
    }

    this.specializationSelected.forEach((value: SpecializationMapper, index) => {
      if (specialization.id === value.id) {
        this.removeSpecializationSelected(index);
        return;
      }
    });
  }

  removeSpecializationSelected(index: number) {
    let specialization = this.specializationSelected[index];
    specialization.checked = false;
    this.specializationSelected.splice(index, 1);
  }

}



